<template>
  <div id="order" @click="hideTab">
    <van-nav-bar fixed style="z-index:1001">
      <div slot="title">
        {{title}}
        <span class="down" @click="show_tab"></span>
        <div class="down_tab" v-if="isShow" @click="changeTitle($event)">
          <span v-on:click="AllOrder(1,100)">淘宝订单</span>
          <!--<span v-on:click="AllOrder(2,100)">京东订单</span>-->
          <span v-on:click="AllOrder(3,100)">拼多多订单</span>
        </div>
      </div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
    </van-nav-bar>
    <van-tabs v-on:click="AllOrders" v-model="active" class="mytab" sticky :offset-top="40" style="z-index:1000">
      <van-tab v-for="(item,items) in orderList" :key="items" :title="item.title">
        <van-list v-model="loading">
          <div class="box" style="margin-top:50px">
            <div v-on:click="cur=0,closeOrder(100)" :class="{active:cur==0}">全部订单</div>
            <div v-on:click="cur=1,closeOrder(1)" :class="{active:cur==1}">已付款</div>
            <div v-on:click="cur=2,closeOrder(5)" :class="{active:cur==2}">已结算</div>
            <div v-on:click="cur=3,closeOrder(4)" :class="{active:cur==3}">已失效</div>
          </div>
          <div class="tab_content" v-if="real">
            <div v-show="cur==0">
              <div class="box3" v-for="(item,index) in datas">
                <div class="box_top">

                  <div class="top_right">
                    订单编号: {{item.trade_id}}
                  </div>
                </div>
                <div class="box_middle">
                  <div class="middle_left">
                    <img :src="item.picUrl" alt="">
                  </div>
                  <div class="middle_right">
                    {{item.title}}
                  </div>
                </div>
                <div class="box_center">
                  <div class="center_left">
                    <p>付款金额</p>
                    <p>￥{{item.payment_price}}</p>
                  </div>
                  <div class="center_right">
                    <p>预估抽成</p>
                    <p style="color:#ff681f">￥{{item.estimated_effect}}</p>
                  </div>
                </div>
                <div class="box_bottom">
                  <div class="bottom_left">
                    {{timestampToTime(item.created_at)}}创建
                  </div>
                  <div class="bottom_right">
                    {{timestampToTime(item.updated_at)}}同步
                  </div>
                </div>
              </div>
            </div>
            <div v-show="cur==1" v-if="real">
              <div class="box3" v-for="(item,index) in datas">
                <div class="box_top">

                  <div class="top_right">
                    订单编号: {{item.trade_id}}
                  </div>
                </div>
                <div class="box_middle">
                  <div class="middle_left">
                    <img :src="item.picUrl" alt="">
                  </div>
                  <div class="middle_right">
                    {{item.title}}
                  </div>
                </div>
                <div class="box_center">
                  <div class="center_left">
                    <p>付款金额</p>
                    <p>￥{{item.payment_price}}</p>
                  </div>
                  <div class="center_right">
                    <p>预估抽成</p>
                    <p style="color:#ff681f">￥{{item.estimated_effect}}</p>
                  </div>
                </div>
                <div class="box_bottom">
                  <div class="bottom_left">
                    {{timestampToTime(item.created_at)}}创建
                  </div>
                  <div class="bottom_right">
                    {{timestampToTime(item.updated_at)}}同步
                  </div>
                </div>
              </div>
            </div>
            <div v-show="cur==2" v-if="real">
              <div class="box3" v-for="(item,index) in datas">
                <div class="box_top">

                  <div class="top_right">
                    订单编号: {{item.trade_id}}
                  </div>
                </div>
                <div class="box_middle">
                  <div class="middle_left">
                    <img :src="item.picUrl" alt="">
                  </div>
                  <div class="middle_right">
                    {{item.title}}
                  </div>
                </div>
                <div class="box_center">
                  <div class="center_left">
                    <p>付款金额</p>
                    <p>￥{{item.payment_price}}</p>
                  </div>
                  <div class="center_right">
                    <p>预估抽成</p>
                    <p style="color:#ff681f">￥{{item.estimated_effect}}</p>
                  </div>
                </div>
                <div class="box_bottom">
                  <div class="bottom_left">
                    {{timestampToTime(item.created_at)}}创建
                  </div>
                  <div class="bottom_right">
                    {{timestampToTime(item.updated_at)}}同步
                  </div>
                </div>
              </div>
            </div>
            <div v-show="cur==3" v-if="real">
              <div class="box3" v-for="(item,index) in datas">
                <div class="box_top">

                  <div class="top_right">
                    订单编号: {{item.trade_id}}
                  </div>
                </div>
                <div class="box_middle">
                  <div class="middle_left">
                    <img :src="item.picUrl" alt="">
                  </div>
                  <div class="middle_right">
                    {{item.title}}
                  </div>
                </div>
                <div class="box_center">
                  <div class="center_left">
                    <p>付款金额</p>
                    <p>￥{{item.payment_price}}</p>
                  </div>
                  <div class="center_right">
                    <p>预估抽成</p>
                    <p style="color:#ff681f">￥{{item.estimated_effect}}</p>
                  </div>
                </div>
                <div class="box_bottom">
                  <div class="bottom_left">
                    {{timestampToTime(item.created_at)}}创建
                  </div>
                  <div class="bottom_right">
                    {{timestampToTime(item.updated_at)}}同步
                  </div>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-tab>
      <!--<van-tab title="I 级订单">内容  1</van-tab>-->
      <!--<van-tab title="II 级订单">内容 3</van-tab>-->
      <!--<van-tab title="团队订单">内容 4</van-tab>-->
    </van-tabs>
  </div>
</template>
import qs from 'Qs';
<script>
  export default {
    name: "order",
    data() {
      return {
        loginType: 'username',
        isHidden: 1,
        urlPath: '',
        cur: 0,
        datas: [],
        loading: true,
        active: 0,
        num: 0,
        isShow: false,
        title: '淘宝订单',
        class: "1",
        spanList: [
          {id: "全部订单"},
          {id: "已付款"},
          {id: "已结算"},
          {id: "已失效"},
        ],
        orderList: [
          {title: '我的订单'},
          {title: 'I 级订单'},
          {title: 'II 级订单'},
          {title: '团队订单'},
        ],
        number: {
          '0': 100,
          '1': 1,
          '2': 5,
          '3': 4,
        },
        length: '',
        real: true,
      }
    },
    mounted() {
      this.AllOrder(this.class, 100);
    },
    methods: {
      AllOrder(tyupe, statu) {
        this.class = tyupe
        var self = this
        let params = {
          type: self.class,
          status: statu,
        }
        this.$POST('/user/order', params, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            this.datas = res.data
          } else {
            this.$toast(res.msg)
          }
          this.length = res.data.length
          if (this.length <= 0 || res.code != 200) {
            this.real = false
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      AllOrders(index) {
        this.cur = 0
        if (index == "0") {
          this.num = 0
          this.urlPath = "order"
        } else if (index == "1") {
          this.num = 1
          this.urlPath = "yiorder"
        } else if (index == "2") {
          this.num = 2
          this.urlPath = "erorder"
        } else if (index == "3") {
          this.num = 3
          this.urlPath = "tdorder"
        }
        var self = this
        let params = {
          type: self.class,
          status: 100,
        }
        this.$POST('/user/' + this.urlPath, params, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            this.datas = res.data;
          } else {
            this.$toast(res.msg)
          }
          this.length = res.data.length
          if (this.length <= 0 || res.code != 200) {
            this.real = false;
          } else {
            this.real = true;
          }
        }).catch(res => {
          this.$toast('订单为空!');
          console.log(res);
        })
      },
      closeOrder(status) {
        if (this.num == "0") {
          this.urlPath = "order"
        } else if (this.num == "1") {
          this.urlPath = "yiorder"
        } else if (this.num == "2") {
          this.urlPath = "erorder"
        } else if (this.num == "3") {
          this.urlPath = "tdorder"
        }
        var self = this
        let params = {
          type: self.class,
          status: status,
        }
        this.$POST('/user/' + this.urlPath, params, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            this.datas = res.data
          } else {
            this.$toast(res.msg)
          }
          this.length = res.data.length
          if (this.length <= 0 || res.code != 200) {
            this.real = false;
          } else {
            this.real = true
          }
        }).catch(res => {
          this.$toast('订单为空!');
          console.log(res);
        })
      },
      show_tab() {
        this.isShow = !this.isShow;
      },
      changeTitle($event) {
        this.active = 0,
          console.log($event.target.innerText);
        this.title = $event.target.innerText;
        this.isShow = false;
      },
      hideTab($event) {
        console.log($event);
        if ($event.target.className !== 'down') {
          this.isShow = false;
        }
      },
      timestampToTime(timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y + M + D + h + m + s;
      },
    }
  }
</script>

<style scoped>
  #order {
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
    font-size: 1rem;
    font-family: PingFang-SC-Regular;
    color: #000000;
  }

  .down {
    display: inline-block;
    width: 0px;
    height: 0px;
    border-width: 0.3rem;
    border-style: solid;
    border-color: black transparent transparent transparent;
  }

  .down_tab {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #ededed;
  }

  .down_tab > span:hover {
    background-color: red;
  }

  .box {
    width: 100%;
    display: flex;
    margin-left: -1px;
  }

  .box > div {
    height: 2rem;
    width: 25%;
    line-height: 2rem;
    text-align: center;
    color: #ff681f;
    border: 1px solid #ff681f;
    border-right: none;
  }

  .box .active {
    background-color: #ff681f;
    color: #ffffff;
  }

  .box2 {
    background-color: #ffffff;
    color: #000000;
    padding: 1rem 1rem;
  }

  .box3 {
    padding: 1rem;
    background-color: #ffffff;
    margin: 1rem 0;
  }

  .box_top, .box_middle, .box_center, .box_bottom {
    margin-bottom: 1rem;
  }

  .box_top {
    display: flex;
    justify-content: space-between;
  }

  .top_left {
    width: 4rem;
    height: 2rem;
    background-color: #c6c5c3;
    text-align: center;
    line-height: 2rem;
    color: #ffffff;
  }

  .top_right {
    height: 2rem;
    line-height: 2rem;
    text-align: right;
  }

  .box_middle {
    display: flex;
    justify-content: space-between;
  }

  .middle_left {
    margin-right: 1rem;
  }

  .middle_left img {
    width: 5rem;
    height: 4rem;
  }

  .middle_right {
    text-align: left;
  }

  .box_center {
    display: flex;
    justify-content: space-between;
  }

  .box_center p {
    margin-bottom: 1rem;
  }

  .box_bottom {
    display: flex;
    justify-content: space-between;
    color: #8a8a8a;
    font-size: 0.8rem;
    border-top: 1px solid #f5f5f5;
    padding: 0.3rem 0;
  }
</style>



